import {
  alpha,
  alphanumeric,
  custom,
  number,
  email,
  max,
  maxLength,
  min,
  minLength,
  required,
  sameAs
} from "@inkline/inkline/validation/validators";
export const validators = {
  alpha,
  alphanumeric,
  custom,
  number,
  email,
  max,
  maxLength,
  min,
  minLength,
  required,
  sameAs
};
export function registerValidator(name, validator) {
  validators[name] = validator;
}
export function unregisterValidator(name) {
  delete validators[name];
}
